.preview {
    position: relative;
    display: block;
    width: 1920px;
    height: 120px;
    -webkit-transform: scale(0.6);
        -ms-transform: scale(0.6);
            transform: scale(0.6);
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    left: calc((100% - 1920px * 0.6)/2);
    border: none;
}


